import React from "react";
import { AppBar, Toolbar, Typography, Drawer, List, ListItemText, Box, TextField, Button } from "@mui/material";
import { ListItemButton } from "@mui/material";

import CallIcon from "@mui/icons-material/Call";
import MessageIcon from "@mui/icons-material/Message";
import VoicemailIcon from "@mui/icons-material/Voicemail";

const menuItems = [
  { text: "Calls", icon: <CallIcon /> },
  { text: "Messages", icon: <MessageIcon /> },
  { text: "Voicemail", icon: <VoicemailIcon /> },
];

const App = () => {
  return (
    <Box sx={{ display: "flex" }}>
      <Drawer variant="permanent" sx={{ width: 240, flexShrink: 0 }}>
        <Toolbar />
        <List>
          {menuItems.map((item) => (
            <ListItemButton component="button" key={item.text} sx={{ display: "flex", alignItems: "center" }}>
              {item.icon}
              <ListItemText primary={item.text} sx={{ marginLeft: 1 }} />
            </ListItemButton>
          ))}
        </List>
      </Drawer>

      <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6">Google Voice Clone</Typography>
          </Toolbar>
        </AppBar>

        <Box sx={{ marginTop: 2 }}>
          <TextField label="Enter number" variant="outlined" fullWidth />
          <Button variant="contained" color="primary" sx={{ marginTop: 2 }}>
            Call
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default App;